@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;0,900;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* style page Screens/Index.js */
/* import */

body{
    height: 100vh;
    background-color: #CDF0EA;
    display: grid;
    align-items: center;
    justify-content: center;
}

.Calc_box_container{
    width: 400px;
    height: 700px;
    background-color: #F6C6EA;
    border-radius: 20px;
    box-shadow: 5px 5px 10px #000;
}
.visor{
    width: 100%;
    height: 30%;
    /*border-radius: 20px; */
    display: grid;
    justify-content: right;
    align-items: flex-end;
    flex-direction: column;
    overflow: hidden !important;
    text-overflow: ellipsis;
    box-shadow: inset -1em -1em 2em -2.3em #38134e;


    
}
.history, .result{
    color: #C490E4;
}

/* Fonts */
.font_robotoRegular{
    font-family: 'Roboto', 'sans-serif';
    font-weight: 400;
}
.font_robotoBold{
    font-family: 'Roboto', 'sans-serif';
    font-weight: 600;
}

/* buttons */
.buttonsView{
    
    height: 70%;
    border-radius: 20px;
    display: grid;
    place-items: center;
    grid-template-rows: auto auto;
}
.buttonColuns{
    display: grid;
    place-items: center;
    grid-template-columns: auto auto auto auto;
}
.buttonColunsTree{
    display: grid;
    place-items: center;
    grid-template-columns: auto auto auto ;
}

.buttonCalc{
    width: 70px;
    height: 70px;
    border: none;
    border-radius: 100%;
    text-align: center;
    font-size: 25pt;
    box-shadow: 5px 5px 10px 0px #38134e;
}
.buttonCalc:hover{
    background-color: #C490E4;
    color: #F9F9F9;
}
.buttonCalc:active{
    box-shadow: 1px 1px 2px 1px #38134e;
}

.buttonBig{
    width: 160px;
    height: 70px;
    border: none;
    border-radius: 50px;
    text-align: center;
    font-size: 30pt;
    
}

/*backgrounds button*/
.buntonBackPurple{
    background-color: #C490E4 ;
    box-shadow: 5px 5px 10px 0px #38134e;
}
.buntonBackPurple:hover{
    background-color: #F9F9F9 ;
    color: #000;
}
.buntonBackPurple:active{
    box-shadow: 1px 1px 2px 1px #38134e;
}
.buntonBackWhite{
    background-color: #F9F9F9 ;
    box-shadow: 5px 5px 10px 0px #38134e;
}
.buntonBackWhite:hover{
    background-color: #C490E4 ;
    color: #F9F9F9;
}
.buntonBackWhite:active{
    box-shadow: 1px 1px 2px 1px #38134e;
}
/* Colors Button */
.buttonColorPurple{
    color: #C490E4;
}
.buttonColorWhite{
    color: #fff;
}
/* debug para facilitar visão */
.debug{
    background-color: azure;
}
.App {
  text-align: center;
}
